import React from 'react';
import firebase from "../../config";
import { ReactTabulator } from "react-tabulator";

class DashScreen extends React.Component{
    constructor(props){
        super(props)
        this.state={
            userCound:null,
            TotalPayout:null,
            columns: [
                { title: "ID", field: "id", width: 100},
                { title: "Email", field: "Email",headerFilter: "input"},
                { title: "First Name", field: "FirstName", },
                { title: "Last Name", field: "LastName", },
                { title: "Phone", field: "Phone", },
                { title: "Product Size", field: "productSize", },
              ],
              values: [],
        }
    }

async componentDidMount(){
    try{
        Promise.all([await this.updateCound(), await this.newFun()]).catch((e) => alert(e.message));
    }catch(e){
        alert(e.message);
    }
}
    newFun = async () =>{
        try{
        var fire =  await firebase.database().ref('users');
        await fire.orderByKey().limitToLast(10).on('value', snapshot =>{
            let list =[];
            snapshot.forEach(snap =>{
                list.push(snap.val());
            });
            console.log(list);
            this.setState({values: list, isLoading: false});
        })
        } catch(e){
            console.log(e.message)
        }
    }

    updateCound = () =>{
        return new Promise(async(resolve) =>{
            var fire = await firebase.database().ref('App').child('Members');
            var pay = await firebase.database().ref('Payout');
            await fire.on('value', (snapshot) =>{
                const snap = snapshot.val();
                this.setState({
                    userCound: snap.Cound,
                })
            })
            await pay.on('value', (snapshot) =>{
                const snap = snapshot.val();
                this.setState({
                    TotalPayout: snap.TotalPayout,
                })
            })
            resolve('resolved');
        })
    }

    render(){
        return(
            <div className="d-flex justify-content-around align-items-center flex-row" style={{width:'95vw',height:'100vh',backgroundColor:"#f6f7fb"}}>
                <div className="col-4 d-flex flex-column justify-content-around align-items-center" style={{height:'90vh'}}>
                    <div className="rounded-lg d-flex justify-content-center align-items-center flex-column bg-primary" style={{height:200,width:'100%'}}>
                        <span style={{fontSize:20,color:'white'}}>{this.state.userCound} Users</span>
                        <span style={{fontSize:29,color:'white',fontWeight:'bold'}}>₹ {this.state.userCound * 4999}</span>
                        <span style={{color:'white'}}>Earnings</span>
                    </div>
                    <div className="rounded-lg d-flex justify-content-center align-items-center flex-column bg-primary" style={{height:200,width:'100%'}}>
                        <span style={{fontSize:25,color:'white',fontWeight:'bold'}}>₹ {this.state.TotalPayout}</span>
                        <span style={{color:'white'}}>Total Pay out</span>
                    </div>
                </div>
                <div className="col-7" style={{height:'90vh',overflowY:'scroll'}}>
                    <ReactTabulator
                    style={{width:'100%'}}
                    className="bg-primary"
                    data={this.state.values}
                    columns={this.state.columns}
                    tooltips={true}
                    layout={"fitData"}/>
                </div>
            </div>
        )
    }
}
export default DashScreen;

