import React from "react";
import firebase from "../../config";
// import 'react-tabulator/lib/styles.css';
import { ReactTabulator } from "react-tabulator";
import {Spinner} from 'react-bootstrap'
// import { CSVLink } from "react-csv";

class UserListScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      values: [],
      columns: [
        { title: "ID", field: "id", width: 100},
        { title: "Email", field: "Email",headerFilter: "input"},
        { title: "First Name", field: "FirstName", },
        { title: "Last Name", field: "LastName", },
        { title: "Phone", field: "Phone", },
        { title: "Product Size", field: "productSize", },
        { title: "Total Revenue", field: "totalMoney", },
        { title: "Revenue", field: "payMoney", },
        { title: "Referal count", field: "totalRefaral", },
        { title: "Downline count", field: "refaral_refaral_count", }
      ],
      isLoading:false,
    };
  }

  async componentDidMount(){
    this.setState({isLoading: true});
    this.newFun();
  }
  // componentWillUnmount(){
  //   console.log("unmounded");
  //   this.setState({status: false});
  // }

  newFun = async () =>{
    console.log("fun call")
    try{
      var fire =  await firebase.database().ref('users');
      await fire.on('value', snapshot =>{
          let list =[];
          snapshot.forEach(snap =>{
              list.push(snap.val());
              console.log("data :"+ list)
          });
          console.log(list);
          this.setState({values: list, isLoading: false});
      })
    } catch(e){
        console.log(e.message)
    }
}

  render() {
    return (
      this.state.isLoading == true ? (
        <div className="d-flex justify-content-center align-items-center" style={{width:'95vw',height:'100vh',backgroundColor:'#f6f7fb'}}>
          <Spinner animation="border" />
        </div>
      ) :(
        <div className="d-flex justify-content-start align-items-start flex-column" style={{width:'95vw',height:'100vh',backgroundColor:'#f6f7fb'}}>
            <div className="bg-primary d-flex justify-content-between align-items-center" style={{ width: '100%', height: 60 }}>
                <span className="font-weight-bold pl-4 mr-3" style={{ fontSize: 20, color: 'white' }}>User List</span>
              <div type="button" className="rounded-circle d-flex justify-content-center align-items-center mr-4" style={{ width: 40, height: 40, backgroundColor: 'white' }}>
                <img src={require('../../img/PANTO.png')} style={{ width: 20 }}></img>
              </div>
            </div>
        <ReactTabulator
        style={{width:'100%'}}
        className="bg-primary"
        data={this.state.values}
        columns={this.state.columns}
        tooltips={true}
        layout={"fitData"}/>
    </div>
      )
    )
  }
}
export default UserListScreen;
