import React from 'react';
import firebase from "../../config";
import { CSVLink } from "react-csv";
// import 'react-tabulator/lib/styles.css';
import { Spinner } from 'react-bootstrap'
import { React15Tabulator } from "react-tabulator";

class BackupScreen extends React.Component{
    constructor(props){
        super(props)
        this.state={
            values: [],
            isLoading: false,
            columns: [
              { title: "RazorpayX Account Number", field: "RazorpayX_Account_Number"},
              { title: "Payout_Amount", field: "Payout_Amount"},
              { title: "Payout Currency", field: "Payout_Currency" },
              { title: "Payout Mode", field: "Payout_Mode" },
              { title: "Payout Purpose", field: "Payout_Purpose" },
              { title: "Payout Narration", field: "Payout_Narration" },
              { title: "Payout Reference Id", field: "Payout_Reference_Id" },
              { title: "Fund Account Id", field: "Fund_Account_Id" },
              { title: "Fund Account Type", field: "Fund_Account_Type" },
              { title: "Fund Account Name", field: "Fund_Account_Name" },
              { title: "Fund Account Ifsc", field: "Fund_Account_Ifsc" },
              { title: "Fund Account Number", field: "Fund_Account_Number" },
              { title: "Fund Account Vpa", field: "Fund_Account_Vpa" },
              { title: "Contact Type", field: "Contact_Type" },
              { title: "Contact Name", field: "Contact_Name" },
              { title: "Contact Email", field: "Contact_Email" },
              { title: "Contact Mobile", field: "Contact_Mobile" },
              { title: "Contact Reference Id", field: "Contact_Reference_Id" },
              { title: "notes place", field: "notes_place" },
              { title: "notes code", field: "notes_code" },
            ],
            csv: [
                { label: "RazorpayX Account Number", key: "RazorpayX_Account_Number"},
                { label: "Payout_Amount", key: "Payout_Amount"},
                { label: "Payout Currency", key: "Payout_Currency" },
                { label: "Payout Mode", key: "Payout_Mode" },
                { label: "Payout Purpose", key: "Payout_Purpose" },
                { label: "Payout Narration", key: "Payout_Narration" },
                { label: "Payout Reference Id", key: "Payout_Reference_Id" },
                { label: "Fund Account Id", key: "Fund_Account_Id" },
                { label: "Fund Account Type", key: "Fund_Account_Type" },
                { label: "Fund Account Name", key: "Fund_Account_Name" },
                { label: "Fund Account Ifsc", key: "Fund_Account_Ifsc" },
                { label: "Fund Account Number", key: "Fund_Account_Number" },
                { label: "Fund Account Vpa", key: "Fund_Account_Vpa" },
                { label: "Contact Type", key: "Contact_Type" },
                { label: "Contact Name", key: "Contact_Name" },
                { label: "Contact Email", key: "Contact_Email" },
                { label: "Contact Mobile", key: "Contact_Mobile" },
                { label: "Contact Reference Id", key: "Contact_Reference_Id" },
                { label: "notes place", key: "notes_place" },
                { label: "notes code", key: "notes_code" },
              ],
        }
    }

    componentDidMount() {
        console.log("mouded");
        this.setState({ isLoading: true });
        this.newFun();
    }

    newFun = async () => {
        try {
            var fire = await firebase.database().ref('Payout').child('Backup');
            await fire.once('value', snapshot => {
                let list = [];
                snapshot.forEach(snap => {
                    list.push(snap.val());
                });
                console.log(list);
                this.setState({ values: list, isLoading: false });
            })
        } catch (e) {
            console.log(e.message)
        }
    }

    render() {
        return (
            this.state.isLoading == true ? (
                <div className="d-flex justify-content-center align-items-center" style={{ backgroundColor: '#f6f7fb', height: '100vh', width: '95vw' }}>
                    <Spinner animation="border" />
                </div>
            ) : (
                    <div style={{ height: '100vh' }}>
                        <div className="bg-primary d-flex justify-content-between align-items-center" style={{ width: '100%', height: 60 }}>
                            <div className="d-flex flex-row justify-content-center align-items-center">
                                <span className="font-weight-bold pl-4 mr-3" style={{ fontSize: 20, color: 'white' }}>Backup</span>
                                <CSVLink data={this.state.values} headers={this.state.csv} filename={"paymentBackup.csv"} className="mr-3 text-primary d-flex justify-content-center align-items-center p-2 rounded-lg text-decoration-none" style={{ backgroundColor: 'white' }}>
                                    <span className="font-weight-bolder">Download CSV</span>
                                </CSVLink>
                            </div>
                            <div type="button" className="rounded-circle d-flex justify-content-center align-items-center mr-4" style={{ width: 40, height: 40, backgroundColor: 'white' }}>
                                <img src={require('../../img/PANTO.png')} style={{ width: 20 }}></img>
                            </div>
                        </div>
                        <React15Tabulator
                            style={{ width: '100%' }}
                            className="bg-primary"
                            data={this.state.values}
                            columns={this.state.columns}
                            tooltips={true}
                            layout={"fitData"}
                        />
                    </div>
                )
            )
        }
    }
export default BackupScreen;